import React from 'react'
import Form, { Field } from 'rc-field-form'

const normalize = v => v.trim()
const rules = [
  { required: true },
  {
    /** AppID  ID */
    validator(_, value) {
      if (!value || /^(\d+$|cls-)/.test(value)) {
        return Promise.resolve()
      }
      return Promise.reject(new Error(' cls-  ID  AppID'))
    }
  }
]

const Search = ({ onSearch }) => {
  const handleFinish = ({ keyword }) => onSearch({ keyword })
  return (
    <Form onFinish={handleFinish}>
      {(_, form) => {
        const keywordErrors = form.getFieldError('keyword')
        return (
          <React.Fragment>
            <Field name="keyword" rules={rules} normalize={normalize}>
              <input placeholder=" AppID  ID " />
            </Field>
            {keywordErrors[0] ? <p className="error">{keywordErrors[0]}</p> : null}
          </React.Fragment>
        )
      }}
    </Form>
  )
}

export default Search
