import React from 'react'

const normalize = v => v.trim()

/** AppID  ID */
const validateKeyword = (value) => {
  if (!value) {
    throw new Error('')
  }
  if (!value || /^(\d+$|cls-)/.test(value)) {
    return true
  }
  throw new Error(' cls-  ID  AppID')
}

const Search = ({ onSearch }) => {
  const [keyword, setKeyword] = React.useState('')
  const [keywordError, setKeywordError] = React.useState(null)
  const handleSubmit = (e) => {
    e.preventDefault()
    try {
      validateKeyword(keyword)
    } catch (error) {
      setKeywordError(error)
      return
    }
    setKeywordError(null)
    onSearch({ keyword })
  }
  return (
    <form onSubmit={handleSubmit}>
      <input value={keyword} onChange={e => setKeyword(normalize(e.target.value))} placeholder=" AppID  ID " />
      {keywordError ? <p className="error">{keywordError.message}</p> : null}
    </form>
  )
}

export default Search
